.movie {
    width: 100%;
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  
  .movie__intro {
    width: 80%;
  }
  
  .movie__backdrop {
    width: 100%;
    height: 500px;
    object-fit: cover;
    object-position: 0 35%;
    align-items: center;
  }
  
  .movie__detail {
    align-items: center;
    width: 75%;
    display: flex;
    position: relative;
    bottom: 225px;
  }
  
  .movie__detailLeft {
    margin-right: 30px;
  }
  
  .movie__poster {
    width: 340px;
    border-radius: 10px;
    box-shadow: rgba(0, 0, 0, 0.86) 0px 22px 40px 6px;
  }
  
  .movie__detailRight {
    color: white;
    display: flex;
    flex-direction: column;
    height: 450px;
    justify-content: space-between;
  }
  
  .movie__detailRightTop > div {
    text-shadow: 0px 0px 5px #000000;
    margin-bottom: 0.5rem;
  }
  
  .movie__name {
    font-weight: 600;
    font-size: 3rem;
  }
  
  .movie__voteCount {
    margin-left: 1rem;
  }
  
  .movie__genres {
    margin: 2.5rem 0;
  }
  
  .movie__genre {
    padding: 0.5rem;
    border: 2px solid white;
    border-radius: 20px;
    margin-right: 1rem;
  }
  
  .movie__detailRightBottom {
    margin: 2rem 0;
    flex: 0.8;
  }
  
  .synopsisText {
    font-size: 1.5rem;
    margin-bottom: 0.5rem;
    font-weight: 600;
    display: flex;
    position: relative;
    align-items: center;
  }
  
  .synopsisText > div:last-of-type {
    margin-left: auto;
  }
  
  .movie__links {
    position: relative;
    bottom: 120px;
    display: flex;
    justify-content: space-between;
    width: 75%;
  }
  
  .movie__heading {
    font-size: 2.2rem;
  }
  
  .movie__Button {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 1.2rem;
    border-radius: 20px;
    cursor: pointer;
    width: 150px;
    color: black;
    font-weight: bold;
  }
  
  .movie__homeButton {
    background-color: rgb(255, 0, 0);
  }
  
  .movie__imdbButton {
    background-color: #f3ce13;
  }
  
  .newTab {
    margin-left: 1.4rem;
  }
  
  .movie__production {
    width: 85%;
    display: flex;
    justify-content: center;
    align-items: flex-end;
    margin-bottom: 4rem;
  }
  
  .movie__productionComapany {
    width: 200px;
    margin: 2rem;
  }
  
  .productionCompanyImage {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }
  
  /* Responsive Styles */
  
  @media (max-width: 768px) {
    .movie__backdrop {
      /* height: 300px; */
      height: auto;
    }
  
    .movie__poster {
      width: 200px;
      height: 0px;
    }
  
    .movie__detail {
      flex-direction: column;
      bottom: initial;
    }
  
    .movie__detailLeft {
      margin-right: 0;
      margin-bottom: 1rem;
    }
  
    .movie__detailRight {
      height: auto;
    }
  
    .movie__heading {
      font-size: 1.8rem;
    }
  
    .movie__production {
      margin-bottom: 2rem;
    }
  
    .movie__productionComapany {
      width: 150px;
      margin: 1rem;
    }
  }
  




.castMembersImage img{
    height: 120px;
    width: 120px;
    size: 1px;
    border-radius: 100%;
    padding-bottom: 0.5rem;
}
  
.castMembersImage {
    display: flex;
    flex-wrap: wrap;
    /* justify-content: flex-start; */
    align-items: center;
    margin: 2rem;
}
  
  .castMember {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 1rem;
  }
  
  .castMemberName {
    margin-top: 5px;
    text-align: center;
  }
  