

.posterImage {
    height: 650px;
  }
  
  .posterImage img {
    margin: auto;
    display: block;
    width: 100%;
  }
  
  .posterImage_overlay {
    position: absolute;
    padding: 3rem;
    height: 70%;
    bottom: 0;
    display: flex;
    flex-direction: column;
    width: 100%;
    justify-content: flex-end;
    align-items: flex-start;
    background-image: linear-gradient(rgba(0, 0, 0, 0), rgba(0, 0, 0, 0));
    opacity: 1;
    transition: opacity 0.3s;
  }
  
  .posterImage_overlay:hover {
    opacity: 1;
  }
  
  .posterImage_title {
    font-weight: 900;
    font-size: 2.2rem;
    margin-bottom: 0.6rem;
    text-align: left;
    color: white;
  }
  
  .releaseDate {
    font-size: 1.4rem;
    margin-bottom: 0.8rem;
    color: white;
  }
  
  .rating {
    margin-left: 1.5rem;
    color: white;
  }
  
  .description {
    font-style: italic;
    font-size: 1rem;
    font-weight: 900;
    text-align: left;
    color: white;
  }
  
  /* Responsive Styles */
  
  @media (max-width: 768px) {
    .posterImage {
      height: auto;
    }
  
    .posterImage img {
      max-width: 100%;
      height: auto;
    }
  
    .posterImage_overlay {
      position: static;
      height: auto;
      padding: 1.5rem;
      margin-top: 1.5rem;
    }
  
    .posterImage_title {
      font-size: 1.8rem;
      text-align: left;
      color: white;
      text-decoration: none;
    }
  
    .releaseDate,
    .rating {
      text-align: left;
      color: white;
      text-decoration: none;
    }
  
    .description {
      text-align: left;
      color: white;
      text-decoration: none;
    }
  }
  