.navbar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: #222;
    opacity: 100%;
    padding: 10px 20px;
  }
  
  .navbar-brand {
    font-weight: bold;
    color: #fff;
    font-weight: 300;
    font-size: 1.2rem;
    opacity: 100%;
    text-decoration: none;
  } 
  /* this is for the logo IDBM */
  
  .navbar-nav {
    display: flex;
    list-style-type: none;
  }
  
  .nav-item {
    margin: 0 10px;
  }
  
  .nav-link {
    color: #fff;
    font-weight: 300;
    font-size: 1.2rem;
    opacity: 100%;
    text-decoration: none;
    transition: color 0.3s ease-in-out;
  }
  /* this is for the other 3 names, popular, rated and upcoming */
  
  .nav-link:hover {
    color: #ea4f4c;
  }
  